import React, { Component } from "react";
import Table from "../../DataMaster/Component/Table";
import { connect } from "react-redux";
import notrespond from "../../Images/500.svg";
import Skeleton from "react-loading-skeleton";
import { Link } from "react-router-dom";

const mapToState = (state) => {
  return {
    KotaList: state.provinsi.kota,
    alert: state.provinsi.alert,
    error: state.provinsi.error,
  };
};
const defaultSorted = [
  {
    dataField: "kode",
    order: "asc",
  },
];
class FormDataHargaEmas extends Component {
  constructor(props) {
    super(props);
    this.state = {
      provinsi: [],
      provinsi_array: [],
      response: "",
      columns: [
        {
          dataField: "kode",
          text: "Kode",
          sort: true,
          headerStyle: () => {
            return { width: "15%" };
          },
        },
        {
          dataField: "Rp",
          text: "Rupiah",
          sort: true,
        },
        {
          dataField: "dollar",
          text: "Dollar",
          sort: true,
        },
        {
          dataField: "Aksi",
          text: "Aksi",
          csvExport: false,
          formatter: (rowContent, row) => {
            return (
              <div className="text-center">
                <button
                  className="btn btn-danger mr-2"
                  onClick={() => alert("KLIK HAPUS")}
                >
                  <i className="fa fa-trash mr-1"></i>
                  HAPUS
                </button>
                <button
                  className="btn btn-warning mr-2"
                  data-toggle="modal"
                  data-target="#editModal"
                  onClick={() => alert("KLIK EDIT")}
                >
                  <i className="fa fa-edit mr-1"></i>
                  EDIT
                </button>
              </div>
            );
          },
        },
      ],
      data: [
        {
          kode: "001",
          Rp: "1000000",
          dollar: "1000",
        },
      ],
    };
  }

  handlerSubmit(data) {
    console.log(data);
  }

  render() {
    return (
      <div className="container-fluid" style={{ color: "black" }}>
        <div id="notif_alert" className={this.props.alert} role="alert">
          <button
            type="button"
            className="close"
            data-dismiss="alert"
            aria-label="Close"
          >
            <span aria-hidden="true">×</span>
          </button>
          <strong>Error - </strong> Tidak Bisa Mengambil Data
        </div>
        <ol className="breadcrumb mb-2">
          <Link to="/parameterSupervisory">
            <button
              type="button"
              className="btn btn-secondary"
              onClick={() => localStorage.clear()}
            >
              <i className="fa fa-angle-left mr-3" aria-hidden="true"></i> Back
            </button>
          </Link>
        </ol>
        <div className="row">
          <div className="col-12">
            <div className="card">
              <div className="card-body">
                <h4 className="card-title mb-3" style={{ fontSize: 30 }}>
                  Data Harga Emas
                </h4>
                <div className="col-lg-12 col-md-12">
                  <div className="text-right">
                    <button
                      name="simpan"
                      id="simpan"
                      type="button"
                      className="btn btn-info"
                      data-toggle="modal"
                      data-target="#primary-header-modal"
                    >
                      Tambah Data
                    </button>
                  </div>
                </div>
                {this.state.data ? (
                  <Table
                    data={this.state.data}
                    columns={this.state.columns}
                    defaultSorted={defaultSorted}
                    namaKey="kodeKota"
                  />
                ) : (
                  <div>
                    {this.props.error ? (
                      <div className="text-center">
                        <img
                          src={notrespond}
                          style={{ width: "30%" }}
                          alt="ACTIVE"
                        />
                        <br />
                        <h4>Request API Gagal, Silahkan Refresh</h4>
                      </div>
                    ) : (
                      <Skeleton width={1100} height={180} />
                    )}
                  </div>
                )}
              </div>
            </div>
          </div>
        </div>
        <div
          id="primary-header-modal"
          className="modal fade"
          tabIndex="-1"
          role="dialog"
          aria-labelledby="primary-header-modalLabel"
          aria-hidden="true"
        >
          <div className="modal-dialog modal-lg">
            <div className="modal-content">
              <div className="modal-header modal-colored-header bg-primary">
                <h4 className="modal-title" id="primary-header-modalLabel">
                  Tambah Data Harga Emas
                </h4>
                <button
                  id="dismiss_modal"
                  name="dismiss_modal"
                  type="button"
                  className="close"
                  data-dismiss="modal"
                  aria-hidden="true"
                >
                  ×
                </button>
              </div>
              <div className="modal-body"></div>
              <div className="modal-footer">
                <button
                  type="button"
                  className="btn btn-light"
                  data-dismiss="modal"
                  id="close_modal"
                >
                  Close
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default connect(mapToState, null)(FormDataHargaEmas);
